@charset "UTF-8";
/*#region Root*/
@import "~@ionic/angular/css/core.css";
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import '~@ionic/angular/css/display.css';
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";
:root {
  --dark-space-cadet: #17173B;
  --space-cadet: #342F4C;
  --state-blue: #6063CC;
  --baby-blue: #B2CAFF;
  --lavander: #E2E2F9;
  --white: #F8F0FF;
  --red: #E03654;
  --yellow: #FACC22;
}

::selection {
  background-color: var(--dark-space-cadet);
  color: var(--white);
}

h3 {
  font-family: "Saira Bold";
  font-size: 1.3em;
  color: var(--dark-space-cadet);
}

p {
  font-family: "Roboto";
}

a {
  font-family: "Roboto";
  color: var(--red);
}

p, h1, h2, h3, h4, h5, h6, span, a {
  margin: 0;
}

/*#endregion*/
/*#region Fontes*/
@font-face {
  font-family: "Saira";
  src: url(../src/assets/FONT/Saira.ttf);
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Saira Italic";
  src: url(../src/assets/FONT/Saira-Italic.ttf);
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Saira Bold";
  src: url(../src/assets/FONT/Saira-Bold.ttf);
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url(../src/assets/FONT/Roboto.ttf);
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Light";
  src: url(../src/assets/FONT/Roboto-Light.ttf);
  font-weight: normal;
  font-style: normal;
}
/*#endregion*/
/*#region Flex e Line*/
.flex {
  display: flex;
  flex-wrap: wrap;
}

.full {
  justify-content: center;
  align-items: center;
  text-align: center;
}

.horizontal {
  justify-content: center;
}

.vertical {
  align-items: center;
}

.line {
  width: 100%;
}

.centraliza {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
}

/*#endregion*/
/*#region Posições*/
.leftA {
  text-align: left;
}

.leftP {
  left: -50%;
}

.centerA {
  text-align: center;
}

.rightA {
  text-align: right;
}

.rightP {
  right: -50%;
}

/*#endregion*/
/*#region On/Off*/
.invisivel {
  display: none;
}

.visiel {
  display: block;
}

/*#endregion*/
/*#region Botão*/
button {
  width: 100%;
  height: 45px;
  font-family: "Saira";
  font-size: 1em;
  outline: none;
  border-radius: 50px;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(122, 122, 165, 0.5);
}

.button {
  width: calc(100vw / 5);
  height: calc(100vw / 5);
  padding: 5px;
  font-size: 0.7em;
}

.important {
  background-color: var(--yellow);
  color: var(--dark-space-cadet);
}

.normal {
  background-color: var(--state-blue);
  color: var(--white);
}

.danger {
  background-color: var(--red);
  color: var(--white);
}

/*#endregion*/
/*#region Input*/
.input {
  background-color: var(--white);
  width: 100%;
  height: 40px;
  margin: 0;
  border: none;
  border-radius: 4px;
  box-sizing: border-box;
  font-family: "Saira";
  color: var(--dark-space-cadet);
}

.aviso {
  font-family: "Saira Italic";
  color: var(--red);
}

/*#endregion*/
/*#region Card*/
.card {
  background-color: var(--dark-space-cadet);
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  padding-top: 20px;
  padding-bottom: 20px;
  box-sizing: border-box;
}

/*#endregion*/
/*#region Triângulo*/
.trianguloUp {
  width: 0;
  height: 0;
  border-left-color: transparent;
  border-left-style: solid;
  border-right-color: transparent;
  border-right-style: solid;
  border-bottom-color: var(--dark-space-cadet);
  border-bottom-style: solid;
}

/*#endregion*/
/*#region Fundo*/
#back {
  background-color: var(--space-cadet);
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0px;
  opacity: 0.5;
  z-index: 100;
}

/*#endregion*/
/*#region Alerta*/
.alert {
  background-color: var(--white);
  width: 90vw;
  padding-bottom: 20px;
  overflow: hidden;
  border-radius: 20px;
  z-index: 200;
}
.alert .top {
  background-color: var(--dark-space-cadet);
  min-height: 8vh;
  overflow: hidden;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  font-size: 1.6em;
  font-family: "Saira Bold";
}
.alert .content {
  width: 90%;
  margin-top: 20px;
  margin-left: 5%;
  color: var(--space-cadet);
  font-size: 1.4em;
}
.alert .content button {
  margin-top: 20px;
}

/*#endregion*/
/*#region NavBar*/
.nav {
  background-color: var(--dark-space-cadet);
  width: 100vw;
  height: calc(100vw / 5);
  position: fixed;
  top: 0px;
  justify-content: space-between;
  padding-left: 5px;
}
.nav img {
  width: 50%;
  margin-left: 5px;
}
.nav .button svg {
  fill: var(--white);
  width: 50%;
}

.menu {
  width: 100vw;
  height: 110vh;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 10;
}
.menu #back {
  width: 30vw;
}
.menu .content {
  background-color: var(--white);
  width: calc(70vw + 1px);
  height: 100vh;
  position: absolute;
  top: 0px;
  right: 0px;
}
.menu .content .top {
  background-color: var(--dark-space-cadet);
  width: 100%;
  height: 13vh;
}
.menu .content .top h3 {
  color: var(--white);
}
.menu .content .top .button {
  margin-left: 10px;
}
.menu .content .top .button svg {
  fill: var(--white);
  width: 60%;
}
.menu .content .mid .itens {
  height: calc(100vw / 5);
  border-top-color: var(--lavander);
  border-top-style: solid;
  border-top-width: 2px;
}
.menu .content .mid .itens h3 {
  color: var(--dark-space-cadet);
  font-family: "Roboto";
  font-size: 1em;
}
.menu .content .mid .button {
  width: calc(80vw / 5);
  height: calc(80vw / 5);
}
.menu .content .mid .button svg {
  fill: var(--state-blue);
  width: 70%;
}

/*#endregion*/
/*#region Tabs*/
.tabs {
  background-color: var(--dark-space-cadet);
  width: 100vw;
  height: calc(100vw / 5);
  position: fixed;
  bottom: 0px;
  font-size: 1em;
}

.tabs .middle {
  padding: 0px;
}

.tabs .middle p {
  position: relative;
  top: calc((-100vw / 5) / 2);
  color: var(--baby-blue);
}

.tabs .flex {
  flex-direction: column;
}

.tabs .button svg {
  fill: var(--white);
  width: 40%;
  margin-bottom: 10px;
}

.tabs .button .principal {
  background-color: var(--baby-blue);
  width: calc(100vw / 5);
  height: calc(100vw / 5);
  position: relative;
  top: calc((-100vw / 5) / 2);
  margin-bottom: 3px;
  border-color: var(--dark-space-cadet);
  border-style: solid;
  border-radius: 100%;
  border-width: 4px;
}

.tabs .button .principal svg {
  fill: var(--dark-space-cadet);
  width: 55%;
  margin: 0px;
}

/*#endregion*/
